@import './_reset', './_font';

html,
body,
#root {
    height: 100%;
}

body {
    background-color: rgb(38, 195, 158);
    // font-family: 'JetBrains Mono', ui-sans-serif, -apple-system, BlinkMacSystemFont, 'Segoe UI', Helvetica, 'Apple Color Emoji', Arial, sans-serif,
    //     'Segoe UI Emoji', 'Segoe UI Symbol', 'Nanum Gothic';
    font-family: 'KyoboHandwriting2021sjy';
    color: #333;

    #root {
        width: 100%;
    }
}
body::-webkit-scrollbar {
    width: 12px; /* 스크롤바의 너비 */
}

body::-webkit-scrollbar-thumb {
    height: 30%; /* 스크롤바의 길이 */
    background: rgb(207, 255, 221); /* 스크롤바의 색상 */
    border-radius: 10px;
}

body::-webkit-scrollbar-track {
    background: rgb(38, 195, 158); /*스크롤바 뒷 배경 색상*/
}

// 모바일에 대한 예외처리를 여기서 한다.
.mobile {
    #Header {
        img {
            width: 70px !important;
        }
    }
    .txt_intro {
        img {
            margin: 0 0 0 10px !important;
        }
    }
}
